import * as restService from "../servers/api"

const model = {
  namespace: 'user',
  state: {
    userInfo: {},
  },
  effects: {
    * userLogin({ payload }, { call, put }) {
      const response = yield call(restService.login, payload);
      yield put({ type: 'save', payload: response });
    },
    * init(action, {
      put
    }) {
      yield delay(2000);
      yield put({
        type: 'caculate',
        delta: 1
      });
    },
  },
  reducers: {
    save(state, action) {
      return {
        ...state,
        userInfo: action.payload.Data
      };
    },
    caculate(state, payload) {
      const {
        count
      } = state;
      const {
        delta
      } = payload;
      return {...state,
        count: count + delta
      };
    },
  },
};

export default model;